@import '../../utils/vars.scss';

.input__wrapper {
    position: relative;
    label  {
        margin-top: 10px;
    }
    input {
    padding: 10px 15px 10px 25px;
    border-radius: 5px;
    outline: none;
    background-color: var(--grey);
    border: 1.5px solid var(--grey);
    width: 100%;
    transition: .3s;
    &:focus, &:hover {
    border: 1.5px solid var(--green);
    background-color: #fff
    }
    }
    .input__icon {
        img {
        width: 18px;
        height: 18px;
        position: absolute;
        left: 5px;
        top: 10px;
    }
}
}